<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  inject: ['reload'],
  name: "admingroup",
  data () {
    return {
      self: this,
      columns: [
          {
              title: 'ID',
              key: 'id',
              align: 'center',
          },
          {
              title: '上级ID',
              key: 'keys',
              align: 'center',
          },
          {
              title: '名称',
              key: 'name',
              align: 'center',
          },
          {
              title: '对应英文',
              key: 'marks',
              align: 'center',
          },
          {
            title: '排序',
            key: 'sort',
            align: 'center',
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "编辑"
                  },
                  props: {
                    type: 'primary',
                    size: 'small',
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.gotomodelEdit(params)
                    }
                  }
                }, 'View'),
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "删除"
                  },
                  props: {
                      type: 'error',
                      size: 'small'
                  },
                  on: {
                      click: () => {
                        this.curMid = params.row.id;
                        console.log(this.curMid)
                        this.warnModal = true
                      }
                  }
                }, 'Delete')
              ]);
            }
          }
      ],
      data: [],
      isLoading: true,
      url1: '',
      url2: '',
      totalPages: 0,
      params: {},
      warnModal: false,
      modal_loading: false,
      curMid: ''
    }
  },
  mounted: function() {
    const token = getToken(),
          params = {"token": token, "page": "0", "per_page": 10};

    this.params = params;
    this.getData(params);
  },
  methods: {
    getData (params) {
      api.modelList(params).then(res => {
        if(res.code === 0) {
          this.data = res.data.models
          this.totalPages = res.data.totlerows
          this.isLoading = false
        } else {
          this.$Notice.error({title: res.msg})
        }
        // console.log(res)
      }).catch(err => {
        this.$Notice.error({title: err})
      })
    },
    gotomodelEdit (params) {
      const mid = params.row.id
      this.$router.push({
        path: '/admin/modelEdit',
        query: {"mid": mid}
      })
    },
    changePage: function(page) {
      // console.log(page)
      this.params.page = page;
      this.getData(this.params);
    },
    changePageNum: function(num) {
      // console.log(num)
      this.params.per_page = num;
      this.getData(this.params);
    },
    adminGroupDel: function() {
      this.modal_loading = true

      const token = getToken(),
            mid = this.curMid,
            params = {"token": token, "mid": mid};

      api.delModel(params).then(res => {
        console.log(res)
        this.warnModal = false
        this.modal_loading = false
        if(res.code == 0) {
          this.$Notice.success({
            "desc": res.msg
          })
          
          this.$store.dispatch("GetIndexMenu").then(res => {
            console.log(res)
          });
          
          this.reload();
        } else {
          this.$Notice.error({
            "desc": res.msg
          })
        }
      }).catch(err => {
        this.warnModal = false
        this.modal_loading = false
        this.$Notice.error({
          "desc": err
        })
      })
    }
  }
}
</script>
<template>
  <div>
    <h3><Icon type="md-grid" size=24 />菜单管理</h3>
    <div class="searchBar">
      <Button type="primary" to="/admin/modelAdd">添加菜单</Button>
    </div>
    <Table border :context="self" :columns="columns" :data="data" :loading=isLoading></Table>
    <Modal v-model="warnModal" width="400">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="ios-information-circle" size=20></Icon>
        <span>删除确认</span>
      </p>
      <div style="text-align:center">
        <p>此操作会删除该菜单下所有子菜单。</p>
        <p>确认删除吗？</p>
      </div>
      <div slot="footer">
        <Button type="error" size="large" long :loading="modal_loading" @click="adminGroupDel">Delete</Button>
      </div>
    </Modal>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalPages*1" show-total show-elevator show-sizer @on-page-size-change="changePageNum" @on-change="changePage" />
      </div>
    </div>
  </div>
    
</template>
<style lang="scss" scoped>
  h3 {
    margin-bottom: 10px;
  }
  .searchBar {
    margin-bottom: 20px;
  }
  .ivu-modal-body {
    p {
      font-size: 16px;
    }
  }
  .ivu-modal-header p {
    font-size: 19px;
  }
</style>
